// Publications

.publications {
 margin-top: 2rem;

 h1 {
 color: var(--link-color);
 font-size: 2rem;
 text-align: center;
 margin-top: 1em;
 margin-bottom: 1em;
 }

 h2 {
 margin-bottom: 1rem;

 span {
 font-size: 1.5rem;
 }
 }

 h2.bibliography {
 color: var(--site-title-color);
 border-top: 1px solid var(--main-border-color);
 padding-top: 0.5rem;
 margin-top: 2rem;
 margin-bottom: -0.5rem;
 text-align: right;
 }

 ol.bibliography {
 list-style: none;
 padding: 0;
 margin-top: 0;

 li {
 margin-bottom: 1rem;

 .preview {
 width: 100%;
 min-width: 80px;
 max-width: 200px;
 }

 .abbr {
 height: 2rem;
 margin-bottom: 0.5rem;

 abbr {
 display: inline-block;
 background-color: var(--sidebar-bg);
 padding-left: 1rem;
 padding-right: 1rem;

 a {
 color: white;

 &:hover {
 text-decoration: none;
 }
 }
 }

 .award {
 color: var(--link-color) !important;
 border: 1px solid var(--link-color);
 }
 }

 .title {
 font-weight: 500;
 }

 .author {
 font-style: italic;

 a {
 border-bottom: 1px dashed var(--link-color);

 &:hover {
 border-bottom-style: solid;
 text-decoration: none;
 }
 }

 > em {
 border-bottom: 1px solid;
 font-style: normal;
 }

 > span.highlight-author {
 font-weight: 500;
 font-style: normal;
 border-bottom: 1px solid var(--link-underline-color);
 }

 > span.more-authors {
 color: var(--text-muted-color);
 border-bottom: 1px dashed var(--text-muted-color);
 cursor: pointer;

 &:hover {
 color: var(--text-color);
 border-bottom: 1px dashed var(--text-color);
 }
 }
 }

 .links {
 .btn {
 font-size: 0.7rem;
 color: var(--text-color);
 border: 1px solid var(--text-color);
 padding: 0.25rem 0.75rem;
 margin-left: 0;
 letter-spacing: 0;
 box-shadow: 1px 1px 0 0 var(--btn-border-color);

 &:hover {
 color: var(--link-color);
 border-color: var(--link-color);

 @extend %tag-hover;
 }

 i {
 vertical-align: middle; /* Align icons with text */
 font-size: 0.6rem;
 }

 i.open {
 display: inline !important; /* Inline display for icons */
 visibility: visible !important; /* Match global .open */
 color: var(--text-muted-color);
 }

 i.hidden {
 display: none !important; /* Properly hide icons */
 visibility: hidden !important; /* Match global .hidden */
 }
 }
 }

 .badges {
 padding-bottom: 0.5rem;

 span {
 display: inline-block;
 color: #000000;
 height: 100%;
 padding-right: 0.5rem;
 vertical-align: middle;

 &:hover {
 text-decoration: underline;
 }
 }
 }

 .hidden {
 display: none;
 font-size: 0.875rem;
 max-height: 0;
 overflow: hidden;
 text-align: justify;
 transition: all 0.15s ease;
 -moz-transition: 0.15s ease;
 -ms-transition: 0.15s ease;
 -o-transition: 0.15s ease;

 p {
 line-height: 1.4em;
 margin: 10px;
 }

 pre {
 font-size: 1em;
 line-height: 1.4em;
 padding: 10px;
 }
 }

 .open {
 display: block;
 font-size: 0.875rem;
 overflow: hidden;
 text-align: justify;
 max-height: 100em;
 transition: all 0.15s ease;
 -moz-transition: 0.15s ease;
 -ms-transition: 0.15s ease;
 -o-transition: 0.15s ease;
 }

 div.abstract.open {
 border: dashed 1px var(--link-color);

 p {
 margin: 0.5rem;
 }
 }

 div.bibtex.open {
 pre {
 margin: 0.5rem;
 }
 }
 }
 }
}
