/*
 * The main dark mode styles
 */

@mixin dark-scheme {
 /* Framework color */
 --main-bg: #191919;
 --mask-bg: rgb(68, 69, 70);
 --main-border-color: #868686;

 /* Common color */
 --text-color: rgb(175, 176, 177);
 --text-muted-color: #868686;
 --text-muted-hightlight-color: #aeaeae;
 --heading-color: #cccccc;
 --label-color: #a7a7a7;
 --blockquote-border-color: rgb(66, 66, 66);
 --blockquote-text-color: #868686;
 --link-color: #7dbbf7;
 --link-underline-color: #0069db;
 --link-hover-color: #d2603a;
 --button-bg: #1e1e1e;
 --btn-border-color: #2e2f31;
 --btn-backtotop-color: var(--text-color);
 --btn-backtotop-border-color: #212122;
 --btn-box-shadow: #555555;
 --card-header-bg: #292929;
 --checkbox-color: rgb(118, 120, 121);
 --checkbox-checked-color: var(--link-color);
 --img-bg: radial-gradient(circle, rgb(22, 22, 24) 0%, rgb(32, 32, 32) 100%);
 --shimmer-bg: linear-gradient(
 90deg,
 rgba(255, 255, 255, 0) 0%,
 rgba(58, 55, 55, 0.4) 50%,
 rgba(255, 255, 255, 0) 100%
 );

 /* Sidebar */
 --site-title-color: #717070;
 --site-subtitle-color: #868686;
 --sidebar-bg: var(--button-bg);
 --sidebar-border-color: #292929;
 --sidebar-muted-color: #868686;
 --sidebar-active-color: rgb(255, 255, 255, 0.95);
 --sidebar-hover-bg: #262626;
 --sidebar-btn-bg: #232328;
 --sidebar-btn-color: #cccccc;
 --avatar-border-color: rgb(206, 206, 206, 0.9);

 /* Topbar */
 --topbar-bg: rgb(27, 27, 30, 0.64);
 --topbar-text-color: var(--text-color);
 --search-border-color: rgb(55, 55, 55);
 --search-icon-color: rgb(100, 102, 105);
 --input-focus-border-color: rgb(112, 114, 115);

 /* Home page */
 --post-list-text-color: rgb(175, 176, 177);
 --btn-patinator-text-color: var(--text-color);
 --btn-paginator-hover-color: #2e2e2e;

 /* Posts */
 --toc-highlight: rgb(116, 178, 243);
 --tag-hover: rgb(43, 56, 62);
 --tb-odd-bg: #252526; /* odd rows of the posts' table */
 --tb-even-bg: rgb(31, 31, 34); /* even rows of the posts' table */
 --tb-border-color: var(--tb-odd-bg);
 --footnote-target-bg: rgb(63, 81, 181);
 --btn-share-color: #bfc1ca;
 --btn-share-hover-color: #d2603a;
 --card-bg: #1e1e1e;
 --card-hovor-bg: #464d51;
 --card-shadow: rgb(21, 21, 21, 0.72) 0 6px 18px 0,
 rgb(137, 135, 135, 0.24) 0 0 0 1px;
 --kbd-wrap-color: #6a6a6a;
 --kbd-text-color: #d3d3d3;
 --kbd-bg-color: #242424;
 --prompt-text-color: rgb(216, 212, 212, 0.75);
 --prompt-tip-bg: rgb(22, 60, 36, 0.64);
 --prompt-tip-icon-color: rgb(15, 164, 15, 0.81);
 --prompt-info-bg: rgb(7, 59, 104, 0.8);
 --prompt-info-icon-color: #0075d1;
 --prompt-warn-bg: rgb(90, 69, 3, 0.88);
 --prompt-warn-icon-color: rgb(255, 165, 0, 0.8);
 --prompt-danger-bg: rgb(86, 28, 8, 0.8);
 --prompt-danger-icon-color: #cd0202;

 /* tags */
 --tag-border: rgb(59, 79, 88);
 --tag-shadow: rgb(32, 33, 33);
 --dash-color: rgb(63, 65, 68);
 --search-tag-bg: #292828;

 /* categories */
 --categories-border: rgb(64, 66, 69, 0.5);
 --categories-hover-bg: rgb(73, 75, 76);
 --categories-icon-hover-color: white;

 /* archives */
 --timeline-node-bg: rgb(150, 152, 156);
 --timeline-color: rgb(63, 65, 68);
 --timeline-year-dot-color: var(--timeline-color);

 .light {
 display: none;
 }

 /* categories */
 .categories.card,
 .list-group-item {
 background-color: var(--card-bg);
 }

 .categories {
 .card-header {
 background-color: var(--card-header-bg);
 }

 .list-group-item {
 border-left: none;
 border-right: none;
 padding-left: 2rem;
 border-color: var(--categories-border);

 &:last-child {
 border-bottom-color: var(--card-bg);
 }
 }
 }

 #archives li:nth-child(odd) {
 background-image: linear-gradient(
 to left,
 rgb(26, 26, 30),
 rgb(39, 39, 45),
 rgb(39, 39, 45),
 rgb(39, 39, 45),
 rgb(26, 26, 30)
 );
 }

 color-scheme: dark;

 /* stylelint-disable-next-line selector-id-pattern */
 #disqus_thread {
 color-scheme: none;
 }

 /* waline: https://waline.js.org/reference/client/style.html */

 /* 常规颜色 */
 --waline-white: #000000;
 --waline-light-grey: #666666;
 --waline-dark-grey: #999999;

 /* 布局颜色 */
 --waline-color: #888888;
 --waline-bg-color: #1e1e1e;
 --waline-bg-color-light: #272727;
 --waline-border-color: #333333;
 --waline-disable-bg-color: #444444;
 --waline-disable-color: #272727;

 /* 特殊颜色 */
 --waline-bq-color: #272727;

 /* 其他颜色 */
 --waline-info-bg-color: #272727;
 --waline-info-color: #666666;
} /* dark-scheme */
