/* The common styles */

html {
 @media (prefers-color-scheme: light) {
 &:not([data-mode]),
 &[data-mode='light'] {
 @include light-scheme;
 }

 &[data-mode='dark'] {
 @include dark-scheme;
 }
 }

 @media (prefers-color-scheme: dark) {
 &:not([data-mode]),
 &[data-mode='dark'] {
 @include dark-scheme;
 }

 &[data-mode='light'] {
 @include light-scheme;
 }
 }

 font-size: 16px;
}

body {
 background: var(--main-bg);
 padding: env(safe-area-inset-top) env(safe-area-inset-right)
 env(safe-area-inset-bottom) env(safe-area-inset-left);
 color: var(--text-color);
 -webkit-font-smoothing: antialiased;
 font-family: $font-family-base;
}

/* --- Typography --- */

@for $i from 1 through 5 {
 h#{$i} {
 @extend %heading;

 @if $i > 1 {
 @extend %section;
 @extend %anchor;
 }

 @if $i < 5 {
 $factor: 0.18rem;

 @if $i == 1 {
 $factor: 0.23rem;
 }

 font-size: 1rem + (5 - $i) * $factor;
 } @else {
 font-size: 1rem;
 }
 }
}

a {
 @extend %link-color;

 text-decoration: none;
}

img {
 max-width: 100%;
 height: auto;
 transition: all 0.35s ease-in-out;

 .blur & {
 $blur: 20px;

 -webkit-filter: blur($blur);
 filter: blur($blur);
 }
}

blockquote {
 border-left: 5px solid var(--blockquote-border-color);
 padding-left: 1rem;
 color: var(--blockquote-text-color);

> p:last-child {
 margin-bottom: 0;
 }

 &[class^='prompt-'] {
 border-left: 0;
 position: relative;
 padding: 1rem 1rem 1rem 3rem;
 color: var(--prompt-text-color);

 @extend %rounded;

 &::before {
 text-align: center;
 width: 3rem;
 position: absolute;
 left: 0.25rem;
 margin-top: 0.4rem;
 text-rendering: auto;
 -webkit-font-smoothing: antialiased;
 }
 }

 @include prompt('tip', '\f0eb', 'regular');
 @include prompt('info', '\f05a');
 @include prompt('warn', '\f071');
 @include prompt('danger', '\f057');
}

kbd {
 font-family: inherit;
 display: inline-block;
 vertical-align: middle;
 line-height: 1.3rem;
 min-width: 1.75rem;
 text-align: center;
 margin: 0 0.3rem;
 padding-top: 0.1rem;
 color: var(--kbd-text-color);
 background-color: var(--kbd-bg-color);
 border-radius: 0.25rem;
 border: solid 1px var(--kbd-wrap-color);
 box-shadow: inset 0 -2px 0 var(--kbd-wrap-color);
}

hr {
 border-color: var(--main-border-color);
}

footer {
 background-color: var(--main-bg);
 height: $footer-height;
 border-top: 1px solid var(--main-border-color);

 @extend %text-xs;

 a {
 @extend %text-highlight;

 &:hover {
 @extend %link-hover;
 }
 }

 em {
 @extend %text-highlight;
 }

 p {
 text-align: center;
 margin-bottom: 0;
 }
}

/* Datamaps */
#world-map {
 position: relative;
 margin: 0 auto;
 max-width: 768px;
 width: 90vw;
 aspect-ratio: 2 / 1;
}

.hoverinfo {
 color: var(--link-hover-color) !important;
}

/* fontawesome icons */
i {
 &.far,
 &.fas {
 @extend %no-cursor;
 }
}

/* --- Panels --- */

.access {
 top: 1rem;
 transition: top 0.2s ease-in-out;
 margin-top: 1rem;
 margin-bottom: 2rem;

 &:only-child {
 position: -webkit-sticky;
 position: sticky;
 }

 > section {
 padding-left: 1rem;
 border-left: 1px solid var(--main-border-color);

 &:not(:last-child) {
 margin-bottom: 2rem;
 }
 }

 .content {
 font-size: 0.9rem;
 }
}

#panel-wrapper {
 /* the headings */
 .panel-heading {
 font-family: inherit;
 line-height: inherit;

 @include label(inherit);
 }

 .post-tag {
 line-height: 1.05rem;
 font-size: 0.85rem;
 border-radius: 0.8rem;
 padding: 0.3rem 0.5rem;
 margin: 0 0.35rem 0.5rem 0;

 &:hover {
 transition: all 0.3s ease-in;
 }
 }
}

#access-lastmod {
 a {
 &:hover {
 @extend %link-hover;
 }

 @extend %no-bottom-border;

 color: inherit;
 }
}

.footnotes > ol {
 padding-left: 2rem;
 margin-top: 0.5rem;

 > li {
 &:not(:last-child) {
 margin-bottom: 0.3rem;
 }

 @extend %sup-fn-target;

 > p {
 margin-left: 0.25em;
 margin-top: 0;
 margin-bottom: 0;
 }
 }
}

.footnote {
 @at-root a#{&} {
 @include ml-mr(1px);
 @include pl-pr(2px);

 border-bottom-style: none !important;
 }
}

sup {
 @extend %sup-fn-target;
}

.reversefootnote {
 @at-root a#{&} {
 font-size: 0.6rem;
 line-height: 1;
 position: relative;
 bottom: 0.25em;
 margin-left: 0.25em;
 border-bottom-style: none !important;
 }
}

/* --- Begin of Markdown table style --- */

/* it will be created by Liquid */
.table-wrapper {
 overflow-x: auto;
 margin-bottom: 1.5rem;

 > table {
 min-width: 100%;
 overflow-x: auto;
 border-spacing: 0;

 thead {
 border-bottom: solid 2px rgba(210, 215, 217, 0.75);

 th {
 @extend %table-cell;
 }
 }

 tbody {
 tr {
 border-bottom: 1px solid var(--tb-border-color);

 &:nth-child(2n) {
 background-color: var(--tb-even-bg);
 }

 &:nth-child(2n + 1) {
 background-color: var(--tb-odd-bg);
 }

 td {
 @extend %table-cell;
 }
 }
 } /* tbody */
 } /* table */
}

/* --- post --- */

.preview-img {
 aspect-ratio: 40 / 21;
 width: 100%;
 height: 100%;
 overflow: hidden;

 @extend %rounded;

 &:not(.no-bg) {
 background: var(--img-bg);
 }

 img {
 height: 100%;
 -o-object-fit: cover;
 object-fit: cover;

 @extend %rounded;

 @at-root #post-list & {
 width: 100%;
 }
 }
}

.post-preview {
 @extend %rounded;

 border: 0;
 background: var(--card-bg);
 box-shadow: var(--card-shadow);

 &::before {
 @extend %rounded;

 content: '';
 width: 100%;
 height: 100%;
 position: absolute;
 background-color: var(--card-hovor-bg);
 opacity: 0;
 transition: opacity 0.35s ease-in-out;
 }

 &:hover {
 &::before {
 opacity: 0.3;
 }
 }
}

main {
 line-height: 1.75;

 h1 {
 margin-top: 2rem;
 }

 p {
 > a.popup {
 &:not(.normal):not(.left):not(.right) {
 @include align-center;
 }
 }
 }

 .categories,
 #tags,
 #archives {
 a:not(:hover) {
 @extend %no-bottom-border;
 }
 }
}

.post-meta {
 @extend %text-sm;

 a {
 &:not([class]):hover {
 @extend %link-hover;
 }
 }

 em {
 @extend %normal-font-style;
 }
}

.content {
 font-size: 1.1rem;
 overflow-wrap: break-word;

 a {
 &.popup {
 @extend %no-cursor;
 @extend %img-caption;
 @include mt-mb(0.5rem);

 cursor: zoom-in;
 }

 &:not(.img-link) {
 @extend %link-underline;

 &:hover {
 @extend %link-hover;
 }
 }
 }

 ol,
 ul {
 &:not([class]),
 &.task-list {
 -webkit-padding-start: 1.75rem;
 padding-inline-start: 1.75rem;

 li {
 padding-left: 0.25rem;
 margin-bottom: 0;
 }

 ol,
 ul {
 -webkit-padding-start: 1.25rem;
 padding-inline-start: 1.25rem;
 margin-bottom: 0.5rem;
 }
 }
 }

 ul {
 margin-bottom: 0.5rem;
 }

 ul.task-list {
 -webkit-padding-start: 1.25rem;
 padding-inline-start: 1.25rem;

 li {
 list-style-type: none;
 padding-left: 0;

 /* checkbox icon */
 > i {
 width: 2rem;
 margin-left: -1.25rem;
 color: var(--checkbox-color);

 &.checked {
 color: var(--checkbox-checked-color);
 }
 }

 ul {
 -webkit-padding-start: 1.75rem;
 padding-inline-start: 1.75rem;
 }
 }

 input[type='checkbox'] {
 margin: 0 0.5rem 0.2rem -1.3rem; // top, right, bottom, left
 vertical-align: middle;
 }
 } /* ul */

 dl {
 dt {
 font-weight: 500;
 }

 dd {
 margin-left: 1rem;
 }
 }

 ::marker {
 color: var(--text-muted-color);
 }

 p {
 margin: 0;
 }

 strong {
 font-weight: 500;
 color: var(--heading-color);
 }

 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
 scroll-margin-top: $topbar-height;
 margin-top: 1rem;
 margin-bottom: 0.5rem;
 }
} /* .content */

.tag:hover {
 @extend %tag-hover;
}

.post-tag {
 display: inline-block;
 min-width: 2rem;
 text-align: center;
 border-radius: 0.5rem;
 border: 1px solid var(--btn-border-color);
 padding: 0 0.4rem;
 color: var(--text-muted-color);
 line-height: 1.3rem;

 &:not(:last-child) {
 margin-right: 0.2rem;
 }
}

.rounded-10 {
 border-radius: 10px !important;
}

.img-link {
 color: transparent;
 display: inline-flex;
}

.shimmer {
 overflow: hidden;
 position: relative;
 background: var(--img-bg);

 &::before {
 content: '';
 position: absolute;
 background: var(--shimmer-bg);
 height: 100%;
 width: 100%;
 -webkit-animation: shimmer 1.3s infinite;
 animation: shimmer 1.3s infinite;
 }

 @-webkit-keyframes shimmer {
 0% {
 transform: translateX(-100%);
 }

 100% {
 transform: translateX(100%);
 }
 }

 @keyframes shimmer {
 0% {
 transform: translateX(-100%);
 }

 100% {
 transform: translateX(100%);
 }
 }
}

.embed-video {
 width: 100%;
 height: 100%;
 margin-bottom: 1rem;
 aspect-ratio: 16 / 9;

 @extend %rounded;

 &.twitch {
 aspect-ratio: 310 / 189;
 }

 &.file {
 display: block;
 width: auto;
 height: auto;
 max-width: 100%;
 max-height: 100%;
 margin: auto;
 margin-bottom: 0;
 }

 @extend %img-caption;
}

/* --- buttons --- */
.btn-lang {
 border: 1px solid !important;
 padding: 1px 3px;
 border-radius: 3px;
 color: var(--link-color);

 &:focus {
 box-shadow: none;
 }
}

/* --- Effects classes --- */

.loaded {
 display: block !important;

 @at-root .d-flex#{&} {
 display: flex !important;
 }
}

.unloaded {
 display: none !important;
 visibility: hidden !important;
 max-width: 0 !important;
 max-height: 0 !important;
}

.visible {
 visibility: visible !important;
}

.open {
 visibility: visible !important;
}

.hidden {
 visibility: hidden !important;
}

.flex-grow-1 {
 flex-grow: 1 !important;
}

.btn-box-shadow {
 box-shadow: var(--btn-box-shadow);
}

/* overwrite bootstrap muted */
.text-muted {
 color: var(--text-muted-color) !important;
}

/* Overwrite bootstrap tooltip */
.tooltip-inner {
 font-size: 0.7rem;
 max-width: 220px;
 text-align: left;
}

/* Overwrite bootstrap outline button */
.btn.btn-outline-primary {
 &:not(.disabled):hover {
 border-color: #007bff !important;
 }
}

.disabled {
 color: rgb(206, 196, 196);
 pointer-events: auto;
 cursor: not-allowed;
}

.hide-border-bottom {
 border-bottom: none !important;
}

.input-focus {
 box-shadow: none;
 border-color: var(--input-focus-border-color) !important;
 background: center !important;
 transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
}

.left {
 float: left;
 margin: 0.75rem 1rem 1rem 0;
}

.right {
 float: right;
 margin: 0.75rem 0 1rem 1rem;
}

/* --- Overriding --- */

/* magnific-popup */

figure .mfp-title {
 text-align: center;
 padding-right: 0;
 margin-top: 0.5rem;
}

.mfp-img {
 transition: none;
}

/* mermaid */
.mermaid {
 text-align: center;
}

/* MathJax */
mjx-container {
 overflow-y: hidden;
 min-width: auto !important;
}

/* Social icons */
.social {
 display: flex;

 a {
 @extend %button;

 &:hover {
 color: var(--link-hover-color) !important;
 background-color: var(--sidebar-hover-bg);
 }

 &:not(:last-child) {
 margin-right: $sb-btn-gap;
 }
 }
}

/* --- sidebar layout --- */

$sidebar-toggle: 'sidebar-toggle';
$toc-toggle: 'toc-toggle';

.mode-toggle {
 @extend %button;
 @extend %sidebar-links;
 @extend %sidebar-link-hover;

 padding: 0;
 border: 0;
 color: var(--sidebar-btn-color);
}

.mode-toggle.btn.light {
 color: #0d47a1; // moon
 border-bottom: 1px solid var(--link-underline-color);
}

.mode-toggle.btn.dark {
 color: #f39c12; // sun
 border-bottom: 1px solid var(--link-underline-color);
}

.icon-border {
 @extend %no-cursor;
 @include ml-mr(calc(($sb-btn-gap - $btn-border-width) / 2));

 background-color: var(--sidebar-btn-color);
 content: '';
 width: $btn-border-width;
 height: $btn-border-width;
 border-radius: 50%;
}

#sidebar-trigger {
 @extend %button;

 display: block;
 border-radius: 5px;
}

#sidebar {
 @include pl-pr(0);

 position: fixed;
 top: 0;
 left: 0;
 height: 100%;
 overflow-y: auto;
 width: $sidebar-width;
 z-index: 99;
 background: var(--sidebar-bg);
 border-right: 1px solid var(--sidebar-border-color);

 /* Hide scrollbar for Chrome, Safari and Opera */
 &::-webkit-scrollbar {
 display: none;
 }

 /* Hide scrollbar for IE, Edge and Firefox */
 -ms-overflow-style: none; /* IE and Edge */
 scrollbar-width: none; /* Firefox */

 %sidebar-link-hover {
 &:hover {
 color: var(--sidebar-active-color);
 }
 }

 a {
 @extend %sidebar-links;
 }

 #avatar {
 display: block;
 width: 7rem;
 height: 7rem;
 overflow: hidden;
 box-shadow: var(--avatar-border-color) 0 0 0 2px;
 transform: translateZ(0); /* fixed the zoom in Safari */

 img {
 transition: transform 0.5s;

 &:hover {
 transform: scale(1.2);
 }
 }
 }

 .profile-wrapper {
 @extend %clickable-transition;

 margin-top: $topbar-height;
 margin-bottom: 1rem;
 padding-left: 0.5rem;
 padding-right: 0.5rem;
 width: 100%;
 display: flex;
 text-align: center;
 flex-direction: column;
 align-items: center;
 justify-content: center;
 }

 .site-title {
 font-family: inherit;
 font-weight: 900;
 font-size: 1.75rem;
 line-height: 1.2;
 letter-spacing: 0.25px;
 margin-top: 1.25rem;
 margin-bottom: 0.5rem;

 a {
 @extend %clickable-transition;
 @extend %sidebar-link-hover;

 color: var(--site-title-color);
 }
 }

 .site-subtitle {
 font-size: 95%;
 color: var(--site-subtitle-color);
 margin-top: 0.25rem;
 word-spacing: 1px;
 -webkit-user-select: none;
 -moz-user-select: none;
 -ms-user-select: none;
 user-select: none;
 }

 .blog-wrapper {
 display: flex;
 align-items: center;
 justify-content: center;
 width: 100%;
 margin-bottom: 2.5rem;

 i {
 font-size: 95%;
 opacity: 0.8;
 }

 a,
 span {
 font-size: 90%;
 letter-spacing: 0.2px;
 }

 a {
 color: var(--sidebar-muted-color);

 &:hover {
 color: var(--sidebar-active-color);
 }

 &.active {
 color: var(--sidebar-active-color);
 }
 }

 .nav-icon {
 @extend %button;

 display: flex;
 align-items: center;
 font-weight: 500;
 height: 2.25rem;
 width: 2.25rem;
 opacity: 0.9;
 margin: 0.5rem 0.25rem;
 color: var(--sidebar-muted-color);
 background-color: var(--sidebar-bg);

 &:hover {
 color: var(--sidebar-active-color);
 background-color: var(--sidebar-hover-bg);
 }

 &.active {
 color: var(--sidebar-active-color);
 background-color: var(--sidebar-hover-bg);
 }
 }

 .nav-buttons {
 display: flex;
 align-items: center;
 justify-content: center;
 width: 100%;

 .active > * {
 color: var(--sidebar-active-color) !important;
 background-color: var(--sidebar-hover-bg);
 }
 }

 .nav-title {
 display: flex;
 align-items: center;
 font-weight: 500;
 font-size: 90%;
 border-radius: 0.75rem;
 opacity: 0.9;
 padding: 0.6rem 1rem;
 color: var(--sidebar-muted-color) !important;
 background-color: var(--sidebar-hover-bg);

 i {
 margin-right: 1rem;
 }

 &:hover > * {
 color: var(--link-hover-color);
 background-color: var(--sidebar-hover-bg);
 }

 &.active > * {
 color: var(--sidebar-active-color) !important;
 background-color: var(--sidebar-hover-bg);
 }
 }

 .nav-contents {
 width: 100%;
 margin: 0.25rem 0.5rem;

 a,
 span {
 color: var(--link-color);

 &:hover {
 color: var(--link-hover-color);
 }
 }

 ul {
 margin: 0;
 padding: 0;
 list-style-position: inside;
 }

 li a {
 margin-left: -0.5rem;
 }

 .tag {
 display: inline-block;
 margin-top: 0.25rem;
 margin-right: 0.25rem;
 }

 .tag-name {
 letter-spacing: 0;
 }

 .nav-categories > .tag {
 border-radius: 0.25rem;
 }
 }
 }

 .nav-item {
 opacity: 0.9;
 width: 100%;
 padding-left: 0.5rem;
 padding-right: 0.5rem;
 margin-top: 0.25rem;

 .nav-link {
 @include pt-pb(0.6rem);

 display: flex;
 align-items: center;
 border-radius: 0.75rem;
 font-weight: 500;

 &:hover {
 color: var(--sidebar-active-color);
 background-color: var(--sidebar-hover-bg);
 }

 i {
 font-size: 95%;
 opacity: 0.8;
 margin-right: 1rem;
 }

 span {
 font-size: 90%;
 letter-spacing: 0.2px;
 }
 }

 &.active {
 .nav-link {
 color: var(--sidebar-active-color);
 background-color: var(--sidebar-hover-bg);

 span {
 opacity: 1;
 }
 }
 }
 }

 .nav-blog,
 .nav-archives,
 .nav-categories,
 .nav-tags {
 display: none;
 }
} /* #sidebar */

@media (hover: hover) {
 #sidebar ul > li:last-child::after {
 transition: top 0.5s ease;
 }

 .nav-link {
 transition: background-color 0.3s ease-in-out;
 }

 .post-preview {
 transition: background-color 0.35s ease-in-out;
 }
}

#search-result-wrapper {
 display: none;
 height: 100%;
 width: 100%;
 overflow: auto;

 .content {
 margin-top: 2rem;
 }
}

/* --- top-bar --- */

#topbar-wrapper {
 display: block;
 position: fixed;
 z-index: 100;
 top: 0;
 left: $sidebar-width;
 right: 0;
 height: $topbar-height;
 background-color: var(--topbar-bg);
}

#topbar {
 #breadcrumb {
 margin-left: 0.5rem;
 font-size: 1rem;
 color: var(--heading-color);
 overflow: hidden;
 text-overflow: ellipsis;
 word-break: keep-all;
 white-space: nowrap;

 // width: clamp(10rem, calc(100vw - $sidebar-width - 10rem), 100%);

 a:hover {
 @extend %link-hover;
 }

 span {
 &:not(:last-child) {
 &::after {
 content: '\f0da';
 font: var(--fa-font-solid);
 font-size: 0.8rem;
 color: var(--text-muted-color);
 }
 }
 }
 }
} /* #topbar */

::-webkit-input-placeholder {
 @include placeholder;
}

::-moz-placeholder {
 @include placeholder;
}

:-ms-input-placeholder {
 @include placeholder;
}

::-ms-input-placeholder {
 @include placeholder;
}

::placeholder {
 @include placeholder;
}

:focus::-webkit-input-placeholder {
 @include placeholder-focus;
}

:focus::-moz-placeholder {
 @include placeholder-focus;
}

:focus:-ms-input-placeholder {
 @include placeholder-focus;
}

:focus::-ms-input-placeholder {
 @include placeholder-focus;
}

:focus::placeholder {
 @include placeholder-focus;
}

#topbar-title {
 display: none;
 font-size: 1.1rem;
 font-weight: 500;
 font-family: sans-serif;
 color: var(--topbar-text-color);
 text-align: center;
 width: 100%;
 overflow: hidden;
 text-overflow: ellipsis;
 word-break: keep-all;
 white-space: nowrap;
 margin-left: 0.5rem;
}

#mask {
 display: none;
 position: fixed;
 inset: 0 0 0 0;
 height: 100%;
 width: 100%;
 z-index: 1;

 @at-root [#{$sidebar-toggle}] & {
 display: block !important;
 }
}

/* --- Panel --- */

/* 'Trigger' button */
#toc-trigger {
 @extend %button;

 display: block;
 border-radius: 5px;
}

/* --- basic wrappers --- */

#main-wrapper {
 position: relative;
 margin-top: $topbar-height;
 max-width: 100%;

 @include pl-pr(0);

 > .container-fluid {
 min-height: 100vh;
 }
}

#topbar-wrapper.row,
#main-wrapper > .container-fluid > .row,
#search-result-wrapper > .row {
 @include ml-mr(0);
}

#tail-wrapper {
 > :not(script) {
 margin-top: 2rem;
 }
}

/* --- button back-to-top --- */

#back-to-top {
 display: none;
 z-index: 1;
 cursor: pointer;
 position: fixed;
 right: 2rem;
 bottom: 1rem;
 background: var(--button-bg);
 color: var(--btn-backtotop-color);
 padding: 0;
 width: $back2top-size;
 height: $back2top-size;
 border-radius: 50%;
 border: 1px solid var(--btn-backtotop-border-color);
 transition: transform 0.2s ease-out;
 -webkit-transition: transform 0.2s ease-out;

 &:hover {
 transform: translate3d(0, -5px, 0);
 -webkit-transform: translate3d(0, -5px, 0);
 }

 i {
 line-height: $back2top-size;
 position: relative;
 bottom: 2px;
 }
}

#notification {
 @-webkit-keyframes popup {
 from {
 opacity: 0;
 bottom: 0;
 }
 }

 @keyframes popup {
 from {
 opacity: 0;
 bottom: 0;
 }
 }

 .toast-header {
 background: none;
 border-bottom: none;
 color: inherit;
 }

 .toast-body {
 font-family: Lato, sans-serif;
 line-height: 1.25rem;

 button {
 font-size: 90%;
 min-width: 4rem;
 }
 }

 &.toast {
 &.show {
 display: block;
 min-width: 20rem;
 border-radius: 0.5rem;
 -webkit-backdrop-filter: blur(10px);
 backdrop-filter: blur(10px);
 background-color: rgba(255, 255, 255, 0.5);
 color: #1b1b1eba;
 position: fixed;
 left: 50%;
 bottom: 20%;
 transform: translateX(-50%);
 -webkit-animation: popup 0.8s;
 animation: popup 0.8s;
 }
 }
}

/* --- markmap --- */

.markmap {
 --markmap-max-width: $main-content-max-width !important;
 --markmap-code-bg: var(--inline-code-bg) !important;
 --markmap-code-color: var(--code-color) !important;
 --markmap-circle-open-bg: var(--card-bg) !important;
 --markmap-text-color: var(--heading-color) !important;
 --markmap-font: $font-family-base !important;

 position: relative;
 font-size: 1rem !important;
 font-family: $font-family-base !important;
}

.markmap > svg {
 width: 90%;
 aspect-ratio: 16 / 9;
}

/*
 Responsive Design:

 {sidebar, content, panel} >= 1200px screen width
 {sidebar, content} >= 850px screen width
 {content} <= 849px screen width

*/

@media all and (max-width: 576px) {
 main {
 .content {
> blockquote[class^='prompt-'] {
 @include ml-mr(-1rem);

 border-radius: 0;
 max-width: none;
 }
 }
 }

 #avatar {
 width: 5rem;
 height: 5rem;
 }
}

@media all and (max-width: 768px) {
 %full-width {
 max-width: 100%;
 }

 #topbar {
 @extend %full-width;
 }

 #main-wrapper > .container-fluid {
 @extend %full-width;
 @include pl-pr(0);
 }
}

@mixin slide($append: null) {
 $basic: transform 0.4s ease;

 @if $append {
 transition: $basic, $append;
 } @else {
 transition: $basic;
 }
}

/* hide sidebar and panel */
@media all and (max-width: 849px) {
 footer {
 @include slide;

 height: $footer-height-large;
 padding: 1.5rem 0;
 }

 [#{$sidebar-toggle}] {
 #sidebar {
 transform: translateX(0);
 }

 #topbar-wrapper,
 #main-wrapper {
 transform: translateX($sidebar-width);
 }

 #back-to-top {
 visibility: hidden;
 }
 }

 #sidebar {
 @include slide;

 transform: translateX(-$sidebar-width); /* hide */
 -webkit-transform: translateX(-$sidebar-width);
 }

 #main-wrapper {
 @include slide;
 }

 #topbar,
 #main-wrapper > .container-fluid {
 max-width: 100%;
 }

 #panel-wrapper {
 display: none;
 }

 #content-wrapper {
 flex: 0 0 100%;
 max-width: 100%;
 padding-right: 0;
 justify-content: center;
 }

 [#{$toc-toggle}] {
 #panel-wrapper {
 display: block;
 position: fixed;
 justify-content: center;
 top: $topbar-height;
 z-index: 2;
 transition: top 0.2s ease-in-out;
 overflow-y: auto;
 width: 100%;
 max-width: 100%; // 强制全宽显示
 max-height: 100vh;
 scrollbar-width: none;
 background-color: var(--sidebar-hover-bg);

 .access {
 display: none;
 }
 }

 #content-wrapper {
 position: relative;
 z-index: 1;
 }
 }

 #search-result-wrapper {
 width: 100%;
 }

 #breadcrumb {
 display: none;
 }

 #topbar-wrapper {
 @include slide(top 0.2s ease);

 left: 0;
 }

 #topbar-title {
 display: block;
 }

 #search-result-wrapper .content {
 letter-spacing: 0;
 }

 #tags {
 justify-content: center !important;
 }

 h1.dynamic-title {
 display: none;

 ~ .content {
 margin-top: 2rem;
 }
 }
} /* max-width: 849px */

/* Sidebar is visible */
@media all and (min-width: 850px) {
 /* Solved jumping scrollbar */
 html {
 overflow-y: scroll;
 }

 #main-wrapper {
 margin-left: $sidebar-width;
 max-width: 100%;
 }

 #sidebar {
 .profile-wrapper {
 margin-top: 3rem;
 }
 }

 #mask {
 display: none;
 visibility: hidden;
 }

 // `sidebar-toggle` means hidden when width > 850px
 [#{$sidebar-toggle}] {
 #sidebar {
 display: none;
 visibility: hidden;
 max-width: 0;
 }

 #topbar-wrapper,
 #topbar,
 #main-wrapper {
 left: 0;
 margin-left: 0;
 max-width: 100%;
 }
 }

 [#{$toc-toggle}] {
 #panel-wrapper {
 display: none;
 }

 #content-wrapper {
 flex: 0 0 100% !important; // 覆盖Bootstrap的col-xl-10
 max-width: 100% !important; // 强制全宽显示
 padding-right: 0 !important; // 移除原有右侧padding
 justify-content: center !important;
 }
 }

 #search-result-wrapper {
 max-width: $main-content-max-width;
 justify-content: start !important;
 }

 div.content .table-wrapper > table {
 min-width: 80%;
 }

 #topbar-title {
 text-align: left;
 }
}

/* Pad horizontal */
