/*
* Mainly scss modules, only imported to `assets/css/main.scss`
*/

/* ---------- scss placeholder --------- */

%heading {
 color: var(--heading-color);
 font-weight: 500;
 font-family: $font-family-heading;
}

%section {
 main & {
 margin-top: 2.5rem;
 margin-bottom: 1.25rem;

 &:focus {
 outline: none; /* avoid outline in Safari */
 }
 }
}

%anchor {
 .anchor {
 font-size: 70%;
 margin-left: -1.5rem;
 padding-right: 0.25rem;
 visibility: visible;
 opacity: 0;
 transition: opacity 0.25s ease-in, visibility 0s ease-in 0.25s;
 }

 @media (hover: hover) {
 .anchor {
 opacity: 0;
 }

 &:hover {
 .anchor {
 opacity: 1;
 }
 }
 }
}

%tag-hover {
 color: var(--link-hover-color);
 background: var(--tag-hover);
 transition: background 0.35s ease-in-out;
}

%table-cell {
 padding: 0.4rem 1rem;
 font-size: 95%;
 white-space: nowrap;
}

%link-hover {
 color: var(--link-hover-color) !important;
 border-bottom: 1px solid var(--link-hover-color);
 text-decoration: none;
}

%link-color {
 color: var(--link-color);
}

%link-underline {
 border-bottom: 1px solid var(--link-underline-color);
}

%clickable-transition {
 transition: all 0.3s ease-in-out;
}

%no-cursor {
 -webkit-user-select: none;
 -moz-user-select: none;
 -ms-user-select: none;
 user-select: none;
}

%no-bottom-border {
 border-bottom: none;
}

%cursor-pointer {
 cursor: pointer;
}

%normal-font-style {
 font-style: normal;
}

%rounded {
 border-radius: $base-radius;
}

%img-caption {
 + em {
 display: block;
 text-align: center;
 font-style: normal;
 font-size: 80%;
 padding: 0;
 color: #6d6c6c;
 }
}

%button {
 display: flex;
 text-align: center;
 align-items: center;
 justify-content: center;
 width: $btn-size;
 height: $btn-size;
 margin-top: $btn-mb;
 margin-bottom: $btn-mb; // multi line gap
 padding: 0;
 border-radius: 50%;
 color: var(--sidebar-muted-color);
 background-color: var(--sidebar-btn-bg);
 border: 0;
 border-bottom: 1px solid var(--link-underline-color);
 transition: transform 0.2s ease-out;
 -webkit-transition: transform 0.2s ease-out;

 &:hover {
 color: var(--sidebar-active-color);
 background-color: var(--sidebar-hover-bg);
 transform: translate3d(0, -2px, 0);
 -webkit-transform: translate3d(0, -2px, 0);
 }
}

%sidebar-links {
 color: var(--sidebar-muted-color);
 -webkit-user-select: none;
 -moz-user-select: none;
 -ms-user-select: none;
 user-select: none;
}

%text-clip {
 display: -webkit-box;
 overflow: hidden;
 text-overflow: ellipsis;
 -webkit-line-clamp: 2;
 -webkit-box-orient: vertical;
}

%text-highlight {
 color: var(--text-muted-hightlight-color);
 font-weight: 500;
}

%text-sm {
 font-size: 0.85rem;
}

%text-xs {
 font-size: 0.8rem;
}

%sup-fn-target {
 &:target {
 background-color: var(--footnote-target-bg);
 width: -moz-fit-content;
 width: -webkit-fit-content;
 width: fit-content;
 transition: background-color 1.75s ease-in-out;
 }
}

/* ---------- scss mixin --------- */

@mixin mt-mb($value) {
 margin-top: $value;
 margin-bottom: $value;
}

@mixin ml-mr($value) {
 margin-left: $value;
 margin-right: $value;
}

@mixin pt-pb($val) {
 padding-top: $val;
 padding-bottom: $val;
}

@mixin pl-pr($val) {
 padding-left: $val;
 padding-right: $val;
}

@mixin placeholder {
 color: var(--text-muted-color) !important;
}

@mixin placeholder-focus {
 opacity: 0.6;
}

@mixin label($font-size: 1rem, $font-weight: 500, $color: var(--label-color)) {
 color: $color;
 font-size: $font-size;
 font-weight: $font-weight;
}

@mixin align-center {
 position: relative;
 left: 50%;
 transform: translateX(-50%);
}

@mixin prompt($type, $fa-content, $fa-style: 'solid') {
 &.prompt-#{$type} {
 background-color: var(--prompt-#{$type}-bg);

 &::before {
 content: $fa-content;
 color: var(--prompt-#{$type}-icon-color);
 font: var(--fa-font-#{$fa-style});
 }
 }
}
