#search-box {
 display: none;
 margin-left: -5rem;
 width: 100%;
}

search {
 display: none;
 width: 100%;
 border-radius: 1rem;
 border: 1px solid var(--main-border-color);
 background: var(--main-bg);
 padding-left: 0.5rem;

 i {
 z-index: 10;
 font-size: 0.9rem;
 color: var(--search-icon-color);
 }
}

/* 'Trigger' button */
#search-trigger {
 @extend %button;

 display: block;
}

/* 'Cancel' link */
#search-cancel {
 @extend %button;

 display: none;
 border: none;
 box-shadow: none;
 background-color: inherit;
 transform: none;
 -webkit-transform: none;

 &:hover {
 background-color: inherit;
 transform: none;
 -webkit-transform: none;
 }
}

#search-input {
 background: center;
 border: 0;
 border-radius: 0;
 padding: 0.18rem 0.3rem;
 color: var(--text-color);
 height: auto;
 width: 100%;
 margin: 0;

 &:focus {
 box-shadow: none;
 }
}

#search-hints {
 padding: 0 1rem;

 h4 {
 margin-bottom: 1.5rem;
 }

 .post-tag {
 display: inline-block;
 line-height: 1rem;
 font-size: 1rem;
 background: var(--search-tag-bg);
 border: none;
 padding: 0.5rem;
 margin: 0 1.25rem 1rem 0;

 &::before {
 content: '#';
 color: var(--text-muted-color);
 padding-right: 0.2rem;
 }

 @extend %link-color;
 }
}

/* variables */
$spacing-unit: 1rem; // 1rem== 16px
$sp-1: $spacing-unit * 0.25;
$sp-2: $spacing-unit * 0.5;
$sp-3: $spacing-unit * 0.75;
$sp-4: $spacing-unit;
$sp-5: $spacing-unit * 1.5;
$sp-6: $spacing-unit * 2;
$sp-7: $spacing-unit * 2.5;
$sp-8: $spacing-unit * 3;
$sp-9: $spacing-unit * 3.5;
$sp-10: $spacing-unit * 4;

#search-results {
 a {
 @extend %link-color;
 @extend %no-bottom-border;
 @extend %heading;

 font-size: 1.4rem;
 }

 a:hover {
 color: inherit !important; // reset a.hover color
 }

 > article {
 width: 100%;

 &:not(:last-child) {
 margin-bottom: 1rem;
 }

 /* icons */
 i {
 color: #818182;
 margin-right: 0.15rem;
 font-size: 80%;
 }

 > p {
 overflow: hidden;
 text-overflow: ellipsis;
 display: -webkit-box;
 -webkit-line-clamp: 3;
 -webkit-box-orient: vertical;
 }
 }
}

.search-results-list {
 width: 100%;
 padding-left: 0;
 margin-bottom: $sp-1;
 list-style: none;
}

.search-results-list-item {
 padding: 0;
 margin: 0;
 border-top: solid 1px var(--main-border-color);

 &:hover {
 color: var(--link-color) !important;
 }
}

.search-result {
 display: block;
 padding: $sp-1 $sp-3;
 scroll-margin-top: $topbar-height;

 &:hover,
 &.active {
 background-color: var(--sidebar-hover-bg);
 }
}

.search-result-title {
 display: block;
 padding-top: $sp-2;
 padding-bottom: $sp-2;
 word-wrap: break-word;

 &:hover {
 color: var(--link-color);
 }
}

.search-result-doc {
 display: flex;
 align-items: center;
 word-wrap: break-word;

 &.search-result-doc-parent {
 opacity: 0.5;
 }

 .search-result-icon {
 width: $sp-4;
 height: $sp-4;
 margin-right: $sp-2;
 color: var(--link-color);
 flex-shrink: 0;
 }

 .search-result-doc-title {
 overflow: auto;
 }
}

.search-result-section {
 margin-left: #{$sp-4 + $sp-2};
 word-wrap: break-word;
 font-size: 80%;
}

.search-result-rel-url {
 display: block;
 margin-left: #{$sp-4 + $sp-2};
 overflow: hidden;
 color: var(--link-hover-color);
 text-overflow: ellipsis;
 white-space: nowrap;
 font-size: 80%;
 font-weight: 400;
}

.search-result-previews {
 display: block;
 padding-left: $sp-4;
 margin-left: $sp-2;
 color: var(--blockquote-text-color);
 font-weight: 400;
 word-wrap: break-word;
 border-left: solid 5px var(--blockquote-border-color);
 font-size: 90%;
}

.search-result-preview + .search-result-preview {
 margin-top: $sp-1;
}

.search-result-highlight {
 font-weight: 500;
 background-color: var(--kbd-bg-color);
 border-radius: 0.25rem;
 border: solid 1px var(--kbd-wrap-color);
 box-shadow: inset 0 -1px 0 var(--kbd-wrap-color);
}

.search-no-result {
 padding: $sp-2 $sp-3;
}

/* bib search */
.bibsearch-form-input {
 color: var(--text-color);
 background: var(--main-bg);
 border: 1px solid var(--main-border-color);
 outline: none;
 margin: 0 10px 0 0;
 width: 100%;
 max-width: 200px;
 min-width: 100px;
 box-sizing: border-box;
 box-shadow: rgba(0, 0, 0, 0.05) 0 1px 2px;
 border-radius: 6px;
}

// highlight-search-term
::highlight(search) {
 background-color: var(--link-underline-color);
 color: var(--text-color);
}
